import React, { useRef, useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import Hls from 'hls.js';
import './WatchSection.css'

const WatchSection = () => {
    const [resolutions, setResolutions] = useState([]);
    const videoRef = useRef(null);
    const location = useLocation();
    const queryParams = new URLSearchParams(location.search);
    const cid = queryParams.get('v');
    const videoUrl = `http://dev.factorzzang.shop:8000/watching/${cid}/hls.m3u8`;
    useEffect(() => {
        if (Hls.isSupported()) {
            const hls = new Hls();
            console.log(cid, videoUrl);
            hls.loadSource(videoUrl);
            hls.attachMedia(videoRef.current);
            hls.on(Hls.Events.MANIFEST_PARSED, (event, data) => {
                const availableResolutions = data.levels.map(level => ({
                    width: level.width,
                    height: level.height,
                }));
                setResolutions(availableResolutions);
                console.log(availableResolutions);
                videoRef.current.play();
            });
        } else if (videoRef.current.canPlayType('application/vnd.apple.mpegurl')) {
            videoRef.current.src = videoUrl;
            videoRef.current.addEventListener('loadedmetadata', () => {
                videoRef.current.play();
            });
        }
    }, [videoUrl]);

    return (
        <div className='watch-container'>
            <div className='video-content'>
                <div className='video-container'>
                    <video ref={videoRef} controls />
                    <div className='info'>
                        <h2>비디오 제목</h2>
                        <div className='user-info'>
                            <div className='user-info'>
                                <img src='./icons/developer.png' />
                                <ul style={{ marginTop: '15px', paddingLeft: '10px', paddingRight: '20px' }}>
                                    <li style={{ marginBottom: '5px' }}>소유자</li>
                                    <li>구독자 10만명</li>
                                </ul>
                                <button>구독</button>
                            </div>
                            <div className='user-info'>
                                <button>좋아요</button>
                                <button>공유</button>
                                <button>더보기</button>
                            </div>
                        </div>
                        <div className='more-info'>설명 및 더보기 버튼</div>
                    </div>
                </div>
                <div className='comment'>
                    댓글 리스트 영역
                </div>
            </div>
            <div className='video-list'>
                비디오 리스트 뷰 영역
            </div>
        </div>
    );
};

export default WatchSection;
