import React, { useState, useEffect } from 'react';
import './VideoMenu.css';

const VideoMenu = ({ options, videoRef, isVisible }) => {
    // 객체의 첫 번째 키를 사용하여 선택된 옵션을 초기화
    const firstKey = Object.keys(options)[0];
    const [selectedOption, setSelectedOption] = useState(options[firstKey]);
    const [playbackRate, setPlaybackRate] = useState(videoRef.current.playbackRate);

    useEffect(() => {
        if (videoRef.current) {
            videoRef.current.playbackRate = playbackRate;
        }
    }, [playbackRate, videoRef]);

    const handleOptionChange = (event) => {
        const selectedValue = event.target.value;
        setSelectedOption(options[selectedValue]);
    };

    const handlePlaybackRateChange = (event) => {
        const rate = parseFloat(event.target.value);
        setPlaybackRate(rate);
        videoRef.current.playbackRate = rate;
    };

    return (
        <div className='VideoMenu'
            style={
                isVisible
                    ? { transform: 'translateX(0%)', opacity: '1' }
                    : { transform: 'translateX(100%)', opacity: '0' }
            } >
            <div className='VideoMenu-Container'>
                <div className='item'>
                    해상도 : &nbsp;
                    <select onChange={handleOptionChange} value={Object.keys(options).find(key => options[key] === selectedOption)}>
                        {
                            Object.keys(options).map((key) => (
                                <option key={key} value={key}>
                                    {options[key].width}x{options[key].height}
                                </option>
                            ))
                        }
                    </select>
                </div>
                <br />
                <div className='item'>재생속도 : &nbsp;
                    <select value={playbackRate.toString()} onChange={handlePlaybackRateChange}>
                        <option value="0.5">0.5x</option>
                        <option value="0.7">0.7x</option>
                        <option value="1">보통속도</option>
                        <option value="1.2">1.2x</option>
                        <option value="1.5">1.5x</option>
                        <option value="2">2.0x</option>
                    </select>
                </div>
            </div>
        </div >
    );
};

export default VideoMenu;
