import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './ContentSection.css';

const ContentSection = () => {
    const navigate = useNavigate();
    const categoryItems = ['전체', '쇼츠', '음악'];
    const [videos, setVideos] = useState([]);
    const [selectedVideo, setSelectedVideo] = useState(null);
    const [selectedCategory, setSelectedCategory] = useState(0);
    const emptyImage = 'data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI0ODAiIGhlaWdodD0iMjQwIiB2aWV3Qm94PSIwIDAgNDgwIDI0MCI+PHJlY3Qgd2lkdGg9IjQ4MCIgaGVpZ2h0PSIyNDAiIGZpbGw9IiNlMGUwZTAiLz48L3N2Zz4=';
    useEffect(() => {
        if (videos.length == 0) {
            axios.get('http://dev.factorzzang.shop:10000/videos')
                .then(response => {
                    setVideos(response.data.list);
                })
                .catch(error => {
                    console.error('Error fetching the video list:', error);
                });
        }
    }, []);

    const handleVideoClick = (video) => {
        setSelectedVideo(video);
        console.log(video);
        const watchUrl = `/watch?v=${video.video_id}`;
        navigate(watchUrl);
    };

    const handleCategoryClick = (category) => {
        setSelectedCategory(category);
        console.log(category);
    };

    return (
        <section>
            <div className="category">
                {categoryItems.map((item, index) => (
                    <p
                        key={index}
                        onClick={() => handleCategoryClick(index)}
                        className={selectedCategory === index ? '' : 'selectable'}
                    >
                        {item}
                    </p>
                ))}
            </div>
            <div className="content">
                <div className="video-grid" id="videoGrid">
                    {videos.length === 0 ? (
                        Array.from({ length: 8 }, (_, index) => (
                            <div className="video-item" key={index}>
                                <img src={emptyImage} alt="Empty Thumbnail" />
                                <div className='profile'>
                                    <img src="./icons/user-circle.svg" />
                                    <div className="text-fields">
                                        <p>Empty Video</p>
                                        <p>제목없음</p>
                                        <p>Unknown</p>
                                        <p>0 views • 0시간 전</p>
                                    </div>
                                </div>
                            </div>
                        ))
                    ) : (
                        videos.map(video => (
                            <div className="video-item" key={video.video_id} onClick={() => handleVideoClick(video)}>
                                <img src={video.thumbnail} alt="Thumbnail" />
                                <div className='profile'>
                                    <img src="./icons/profile.png" />
                                    <div className="text-fields">
                                        <p className='bold'>{video.video_id}</p>
                                        <p>{video.description}</p>
                                        <p>{video.user_id}</p>
                                        <p>{video.view_count} views • 12시간 전</p>
                                    </div>
                                </div>
                            </div>
                        ))
                    )}
                </div>
            </div>
        </section>
    );
};

export default ContentSection;