import { atom } from 'recoil';

export const isPlayingState = atom({
    key: 'isPlayingState',
    default: false,
});

export const volumeState = atom({
    key: 'volumeState',
    default: 0.5,
});

export const currentTimeState = atom({
    key: 'currentTimeState',
    default: 0,
});

export const durationState = atom({
    key: 'durationState',
    default: 0,
});

export const objectfitState = atom({
    key: 'objectfitState',
    default: 'contain',
});

export const isVideoMenuState = atom({
    key: 'isVideoMenuState',
    default: false,
});


export const isFullscreenState = atom({
    key: 'isFullscreenState',
    default: false,
});
