import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Link, Routes, Route, useNavigate } from 'react-router-dom';
import SideMenu from '../components/SideMenu';
import ContentSection from '../components/ContentSection';
import WatchSection from '../components/WatchSection';
import MyBox from '../components/MyBox';
import './MainPage.css';
import { AccountCircle, Notifications } from '@mui/icons-material';

const MainPage = () => {
  const navigate = useNavigate();
  let [menuState, setMenuState] = useState({
    isVisible: true,
    isCompact: false,
    isAnimating: false,
  });
  let [loginState, setLoginState] = useState(false);
  let [deviceWidth, setDeviceWidth] = useState(window.innerWidth);
  let [deviceHeight, setDeviceHeight] = useState(window.innerHeight);

  function menuStyleChange() {
    // console.log('menustyleChange');
    setMenuState(prevState => ({
      ...prevState,
      isAnimating: true,
      isCompact: !prevState.isCompact,
    }));
  }
  const authLink = () => {
    if (loginState) {
      localStorage.removeItem('access-token');
      setLoginState(false);
    }
    else {
      navigate('/login');
    }
  };
  const logWindowSize = () => {
    setDeviceWidth(window.innerWidth);
    setDeviceHeight(window.innerHeight);
    // console.log(`Width: ${deviceWidth}, Height: ${window.innerHeight}`);
  };

  useEffect(() => {
    window.addEventListener('resize', logWindowSize);
    const token = localStorage.getItem('access-token');
    setLoginState(!!token);
    return () => {
      window.removeEventListener('resize', logWindowSize);
    };
  }, []);

  const loginText = loginState ? "로그아웃" : "로그인";
  const profileimg = "./icons/profile.png";
  const imgStyle = { width: '25px', height: '25px', filter: 'none', marginRight: '5px' };

  return (
    <div className="App">
      <div className='ActionBar'>
        <Link to="/">
          <div className='LogoBar'>
            <img src="./icons/hamburger.png" alt="menustyle" className="Setup" onClick={menuStyleChange} />
            <h1>React&nbsp;<div className='Green'>FactorzWeb</div></h1>
          </div>
        </Link>
        <div className='SearchBar'>
          <input type="text" placeholder="검색어를 입력하세요" />
          <img src="./icons/magnifying.svg" alt="Search Icon" />
        </div>
        <div className='InfoBar'>
          <div className='menu'><Notifications style={{ marginRight: '5px' }} />알림</div>
          <div className='menu' id="auth-link" onClick={authLink}>
            {loginState ? <img src={profileimg} style={imgStyle} /> : <AccountCircle style={{ marginRight: '5px' }} />}
            {loginText}
          </div>
        </div>
      </div >
      <div className='Content'>
        <SideMenu menuState={menuState} setMenuState={setMenuState} />
        <Routes>
          <Route path="/" element={<ContentSection />} />
          <Route path="/watch" element={<WatchSection />} />
          <Route path={"/myPage/:uid"} element={<MyBox />} />
        </Routes>

      </div>
      <div className='StatusBar'>
        &copy;&nbsp;<p>2024</p>&nbsp;<div className='Green'>FactorzWeb</div>&nbsp;<p>테스트 페이지.</p>&nbsp;<div className='Yellow'>factorzzang</div>&nbsp;<p>모든 권리 보유. screen({deviceWidth} x {deviceHeight})</p>
      </div>
    </div >
  );
}

export default MainPage;
