import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import { Home, LocalFireDepartment as Hotclip, AccountCircle as Mybox, CloudUpload as Upload } from '@mui/icons-material';
import { Videocam, Settings, Info, Menu } from '@mui/icons-material';
import './SideMenu.css';


const SideMenu = ({ menuState, setMenuState }) => {
  const menuBarRef = useRef(null);
  var width = menuState.isCompact ? 50 : 150;
  var itemClassName = menuState.isCompact || menuState.isAnimating ? "compact-item" : "menu-item";
  console.log(`itemClassName: ${itemClassName}`);
  console.log(`isCompact: ${menuState.isCompact}`);
  console.log(`isAnimating: ${menuState.isAnimating}`);
  useEffect(() => {
    const handleTransitionEnd = (event) => {
      if (event.propertyName === 'width') {
        itemClassName = menuState.isCompact ? "compact-item" : "menu-item";
        setMenuState(prevState => ({
          ...prevState,
          isAnimating: false,
          isCompact: prevState.isCompact,
        }));
        console.log(`transitionEnd isAnimating: ${menuState.isAnimating}`);
      }
    };

    const menuBarElement = menuBarRef.current;
    if (menuBarElement) {
      menuBarElement.addEventListener('transitionend', handleTransitionEnd);
    }

    return () => {
      if (menuBarElement) {
        menuBarElement.removeEventListener('transitionend', handleTransitionEnd);
      }
    };
  }, []);

  return (
    <div ref={menuBarRef} className='menu-bar' style={{ width: `${width}px` }}>
      <Link to="/" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Home />홈</div>
      </Link>
      <Link to="/watch?v=100035" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Hotclip />핫클립</div>
      </Link>
      <hr />
      <Link to="/myPage/test" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Mybox />내보관함</div>
      </Link>
      <Link to="#" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Upload />업로드</div>
      </Link>
      <hr />
      <Link to="/player" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Videocam />플레이어</div>
      </Link>
      <Link to="/test" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Settings />테스트</div>
      </Link>
      <Link to="#" style={menuState.isCompact ? { justifyContent: 'center' } : {}}>
        <div className={itemClassName}><Info />고객센터</div>
      </Link>
    </div>
  );
};


export default SideMenu;
