import React, { useState, useEffect, useRef } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useRecoilState } from 'recoil';
import { PlayArrow, Pause, VolumeUp, VolumeDown, VolumeOff } from '@mui/icons-material';
import { FullscreenOutlined, FullscreenExitOutlined, FitScreenOutlined } from '@mui/icons-material';
import { Settings, PictureInPictureAlt } from '@mui/icons-material';
import './VideoController.css';
import {
    isPlayingState,
    volumeState,
    isFullscreenState,
    objectfitState,
    isVideoMenuState,
    currentTimeState,
    durationState,
} from '../providers/videoState';


function formatTime(time) {
    if (isNaN(time) || time == 0) return "00:00";

    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);

    return (
        String(minutes).padStart(2, "0") + ":" + String(seconds).padStart(2, "0")
    );
}

const VideoController = ({ isPlayerHovered, videoContainer, video, totaltime }) => {
    // useRef, useState
    const controllerRef = useRef(null);
    const videoSeekBarRef = useRef(null);
    const volumeSeekBarRef = useRef(null);
    const [isPrePlay, setIsPrePlay] = useState(false);
    const [isVideoDragging, setIsVideoDragging] = useState(false);
    const [isVolumeHovered, setIsVolumeHovered] = useState(false);
    const [isVolumeDragging, setIsVolumeDragging] = useState(false);
    const [currentTime, setCurrentTime] = useState(0);
    const [duration, setDuration] = useState(totaltime);

    // useRecoilState
    const [objectfit, setObjectfit] = useRecoilState(objectfitState);
    const [isPlaying, setIsPlaying] = useRecoilState(isPlayingState);
    const [volume, setVolume] = useRecoilState(volumeState);
    const [isFullscreen, setIsFullscreen] = useRecoilState(isFullscreenState);
    const [isVideoMenu, setIsVideoMenu] = useRecoilState(isVideoMenuState);

    useEffect(() => {
        video.current.volume = volume;
    }, [volume]);


    useEffect(() => {
        const updatePlayingState = () => {
            setIsPlaying(true);
        }
        const updatePauseState = () => {
            setIsPlaying(false);
        }
        const updateTime = () => {
            if (video.current) {
                setCurrentTime(video.current.currentTime);
                setDuration(video.current.duration);
            }
        };
        video.current.addEventListener('timeupdate', updateTime);
        video.current.addEventListener('playing', updatePlayingState);
        video.current.addEventListener('pause', updatePauseState);
        return () => {
            if (video.current) {
                video.current.removeEventListener('timeupdate', updateTime);
                video.current.removeEventListener('playing', updatePlayingState);
                video.current.removeEventListener('pause', updatePauseState);
            }
        };
    }, [video]);

    if (!isVolumeHovered && isVolumeDragging) {
        setIsVolumeDragging(false);
    }

    const handleVideoDragStart = (e) => {
        const seekBarRect = videoSeekBarRef.current.getBoundingClientRect();
        const seekBarLeft = seekBarRect.left;
        const seekBarRight = seekBarRect.right;
        if (!video.current.paused) {
            video.current.pause();
            setIsPrePlay(true);
        }
        console.log(seekBarRect);
        if (e.clientX >= seekBarLeft && e.clientX <= seekBarRight) {
            setIsVideoDragging(true);
            const newPosition = Math.round((e.clientX - seekBarLeft) / seekBarRect.width * duration) / duration * duration;
            console.log("start newpos:", newPosition);
            setCurrentTime(newPosition);
            video.current.currentTime = newPosition;
        }
        else if (e.clientX < seekBarLeft) {
            setCurrentTime(0);
        }
        else if (e.clientX > seekBarRight) {
            setCurrentTime(duration);
        }
    };

    const handleVideoDrag = (e) => {
        e.preventDefault();
        if (!isPlayerHovered) {
            setIsVideoDragging(false);
        } else if (isVideoDragging) {
            const seekBarRect = videoSeekBarRef.current.getBoundingClientRect();
            const seekBarLeft = seekBarRect.left;
            const seekBarRight = seekBarRect.right;
            console.log(seekBarRect);
            if (e.clientX >= seekBarLeft && e.clientX <= seekBarRight) {
                const newPosition = Math.round((e.clientX - seekBarLeft) / seekBarRect.width * duration) / duration * duration;
                setCurrentTime(newPosition);
                video.current.currentTime = newPosition;
            }
            else if (e.clientX < seekBarLeft) {
                setCurrentTime(0);
                video.current.currentTime = 0;
            }
            else if (e.clientX > seekBarRight) {
                setCurrentTime(duration);
                video.current.currentTime = duration;
            }
        }
    };

    const handleVideoDragEnd = () => {
        console.log("handleVideoDragEnd", isPrePlay);
        setIsVideoDragging(false);
        if (isPrePlay) {
            video.current.play();
            setIsPrePlay(false);
        }
    };

    const handlePlay = () => {
        if (video.current.paused) {
            video.current.play();
        } else {
            video.current.pause();
        }
    };

    const handleFitScreen = () => {
        setObjectfit((prevFit) => (prevFit === 'contain' ? 'cover' : 'contain'));
    };

    const handleFullScreen = () => {
        if (isFullscreen) {
            setIsFullscreen((prevFullscreen) => (!prevFullscreen));
            if (document.exitFullscreen) {
                document.exitFullscreen();
            }
        }
        else {
            setIsFullscreen((prevFullscreen) => (!prevFullscreen));
            if (videoContainer.current.requestFullscreen) {
                videoContainer.current.requestFullscreen();
            }
        }
    };

    const handleSetupMenu = () => {
        setIsVideoMenu((prev) => (!prev));
    };

    const handleVolumeDragStart = (e) => {
        const seekBarRect = volumeSeekBarRef.current.getBoundingClientRect();
        const seekBarLeft = seekBarRect.left;
        const seekBarRight = seekBarRect.right;
        if (e.clientX >= seekBarLeft && e.clientX <= seekBarRight) {
            setIsVolumeDragging(true);
            const newVolume = Math.round((e.clientX - seekBarLeft) / seekBarRect.width * 10) / 10;
            setVolume(newVolume > 0.9 ? 1 : newVolume < 0.1 ? 0 : newVolume);
        }
    };

    const handleVolumeDrag = (e) => {
        if (isVolumeDragging) {
            e.preventDefault();

            const seekBarRect = volumeSeekBarRef.current.getBoundingClientRect();
            const seekBarLeft = seekBarRect.left;
            const seekBarRight = seekBarRect.right;
            if (e.clientX >= seekBarLeft && e.clientX <= seekBarRight) {
                const newVolume = Math.round((e.clientX - seekBarLeft) / seekBarRect.width * 10) / 10;
                setVolume(newVolume > 0.9 ? 1 : newVolume < 0.1 ? 0 : newVolume);
            }
        }
    };

    const handleVolumeDragEnd = () => {
        setIsVolumeDragging(false);
    };

    const handlePipMode = () => {
        if (video.current.requestPictureInPicture) {
            video.current.requestPictureInPicture();
        } else {
            console.log("test");
        }
    };

    return (
        <div className='VideoController'
            ref={controllerRef}
            style={
                isPlayerHovered
                    ? { opacity: '1', transform: 'translateY(0%)', }
                    : { opacity: '0', transform: 'translateY(100%)', }
            }>
            <div className='ProgressBar'
                ref={videoSeekBarRef}
                onMouseDown={handleVideoDragStart}
                onMouseMove={handleVideoDrag}
                onMouseUp={handleVideoDragEnd}>
                <div className='seek-bar' >
                    <div className='seek' style={{ width: `${(currentTime / duration) * 100}%` }} />
                </div>
            </div>
            <div className='ControlButton'>
                <div className='PlayBox'>
                    {
                        isPlaying
                            ? <Pause className="icon" onClick={handlePlay} />
                            : <PlayArrow className="icon" onClick={handlePlay} />
                    }
                    <div className="VolumeBox"
                        onMouseDown={handleVolumeDragStart}
                        onMouseMove={handleVolumeDrag}
                        onMouseUp={handleVolumeDragEnd}
                        onMouseEnter={() => setIsVolumeHovered(true)}
                        onMouseLeave={() => setIsVolumeHovered(false)}>
                        {
                            volume > 0 ?
                                (
                                    volume > 0.5
                                        ? <VolumeUp className="icon" onClick={() => setVolume(0)} />
                                        : <VolumeDown className="icon" onClick={() => setVolume(0)} />
                                )
                                : <VolumeOff className="icon" onClick={() => setVolume(0.5)} />
                        }
                        {
                            <div className={`seek-bar ${isVolumeHovered ? 'visible' : 'hidden'}`} ref={volumeSeekBarRef}>
                                <div className="seek" style={{ width: `${volume * 100}%` }} />
                                <div className="thumb" style={{ left: `calc(${volume * 100}% - 5px)` }} />
                            </div>
                        }
                    </div>
                    <p>{formatTime(currentTime)} / {formatTime(duration)}</p>
                </div>
                <div style={{ gap: '60px' }}>
                    <FitScreenOutlined className="icon" onClick={handleFitScreen} />
                    {
                        isFullscreen
                            ? <FullscreenExitOutlined className="icon" onClick={handleFullScreen} />
                            : <FullscreenOutlined className="icon" onClick={handleFullScreen} />
                    }
                    <PictureInPictureAlt className='icon' onClick={handlePipMode} />
                    <Settings className="icon" onClick={handleSetupMenu} />
                </div>
            </div>
        </div>
    );
};

export default VideoController;